<template>
  <div
    class="perchBox boxBorder"
    :class="active"
    @mouseover="mouseOver"
    @mouseleave="mouseLeave"
    :style="{background:itemData.backgroundColor,height:itemData.height+'px'}"
  >
      <!-- <p>空白占位组件</p>
      <p>店铺标题：{{itemData.title}}</p>
      <p>店铺页面描述：{{itemData.describe}}</p>-->
    <!-- 左侧介绍 -->
    <span class="box-title">{{itemData.name}}</span>
    <i class="delete-btn" v-if="deleteBtn" @click.stop="deleteBox(itemData)">
      <img src="@/assets/decorateBox/deleteBox.png" alt />
    </i>
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false
    };
  },
  methods: {
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

<style scoped lang="less">
.delete-btn {
  position: absolute;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 3;
  top: 10px;
  right: -32px;
}
.box-title {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  top: 0px;
  left: -70px;
  height: 24px;
  font-size: 12px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 2px 2px 4px 0px rgba(80, 80, 80, 0.2);
  color: rgba(102, 102, 102, 1);
  line-height: 24px;
  width: 64px;
  text-align: center;
  border-radius: 2px;
}
.box-title:after,
.box-title:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  left: 50%;
  margin-left: 32px;
  bottom: 8px;
}

.box-title:after {
  border-left-color: #fff;
}

</style>
